<template>
    <div style="min-width: 865px">
        <div class="tree-tr tree-title">
            <div>
                <span style="padding-left: 20px;padding-right: 20px">部门</span>
                <el-button size="mini" type="success" round @click="append(null)" v-checkup="':sys:menu:add'">增加</el-button>
            </div>
            <div>
                <span class='tree-big-td'>操作
                </span><span class="tree-td">上级部门
            </span><span class="tree-td">排序
            </span>
            </div>
        </div>
        <el-tree
            :data="dataList"
            :props="defaultProps"
            node-key="menuId"
            accordion
            :expand-on-click-node="false"
            :render-content="renderContent">
        </el-tree>



        <!--修改弹框-->
        <el-dialog title="修改菜单" :visible.sync="editDialog">
            <el-form :model="form">
                <el-form-item label="部门名称" :label-width="formLabelWidth">
                    <el-input v-model="form.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="排序号" :label-width="formLabelWidth" v-if="addform.type!=2">
                    <el-input v-model="form.orderNum" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editFinsh()">确 定</el-button>
            </div>
        </el-dialog>

        <!--新增弹框-->
        <el-dialog title="新增菜单" :visible.sync="addDialog">
            <el-form :model="addform">
                <el-form-item label="部门名称" :label-width="formLabelWidth">
                    <el-input v-model="addform.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="排序号" :label-width="formLabelWidth" v-if="addform.type!=2">
                    <el-input v-model="addform.orderNum" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="addFinsh()">确 定</el-button>
            </div>
        </el-dialog>


    </div>
</template>


<script type="text/jsx">
    import icon from './icon'
    let id = 1000;
    let editData={};
    let addData={};

    export default {
        data() {
            return {
                dataList: [],
                defaultProps: {
                    children: 'list',
                    label: 'name'
                },
                editDialog: false,
                addDialog:false,
                form: {
                    name:'',
                    orderNum:'',
                },
                addform: {
                    name:'',
                    orderNum:'',
                    menuId:(new Date()).getTime()
                },
                formLabelWidth: '80px',
            }
        },
        mounted(){
            this.dataList=this.$store.state.deptList['deptList'];
        },
        components:{
            icon
        },
        methods: {
            editFinsh(){
                this.editDialog=false;
                editData.name=this.form.name;
                editData.icon=this.form.icon;
                editData.orderNum=this.form.orderNum;
                editData.url=this.form.url;
                editData.perms=this.form.perms;
                this.$store.commit('changeDeptList',{'deptList':this.dataList})
            },
            edit(node, data){
                this.editDialog=true;
                editData=data;
                this.form=JSON.parse(JSON.stringify(data));
            },
            addFinsh(){
                if(this.addform.type==2){
                    this.addform.icon=''
                }
                if(addData){
                    if (!addData.list) {
                        this.$set(addData, 'list', []);
                    }
                    addData.list.push(this.addform);
                }else{
                    this.dataList.push(this.addform);
                }
                this.addDialog=false;
                this.$store.commit('changeDeptList',{'deptList':this.dataList})
            },
            append(data) {
                this.addDialog=true;
                addData=data;
                this.addform= {
                    name:'',
                    icon:'el-icon-tickets',
                    orderNum:'',
                    url:'',
                    perms:'',
                    type:0,
                    menuId:(new Date()).getTime()
                }
            },
            remove(node, data) {

                this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    const parent = node.parent;
                    const children = parent.data.list || parent.data;
                    const index = children.findIndex(d => d.menuId === data.menuId);
                    children.splice(index, 1);
                    this.$store.commit('changeDeptList',{'deptList':this.dataList})
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });


            },
            renderContent(h, { node, data, store }) {
                return (
                    <div class='tree-tr'>
                        <div>
                            <span>{data.name}</span>
                        </div>
                        <div>
                        <span class='tree-big-td'>
                            <el-button size="mini" type="danger" round on-click={ () => this.remove(node, data) }>删除</el-button>
                            <el-button size="mini" type="primary" round on-click={ () => this.edit(node, data) }>修改</el-button>
                            <el-button  size="mini" type="success" round on-click={ () => this.append(data) }>增加</el-button>
                        </span>
                            <span class='tree-td'>{data.url}</span>
                            <span class='tree-td'>{data.perms}</span>
                        </div>
                    </div>);
            }
        }
    };
</script>
<style lang="less">
    .el-dialog{
        .el-input,.el-select{
            width: 300px;
        }
    }
    .el-tree-node__content{
        height: 37px;
    }
    .el-tree-node{
        border: 1px solid #ddd;

        .el-tree-node{
            border-bottom: none;
            border-left: none;
            border-right: none;
        }

    }
    .tree-td{
        width: 80px;
        display: inline-block;
        border-left: 1px solid #ddd;
        padding-right: 8px;
        padding-left: 8px;
        height: 37px;
        line-height: 37px;
        vertical-align: middle;
        text-align: center;
        box-sizing: border-box;
        overflow: hidden;
    }
    .tree-big-td{
        .tree-td;
        width: 200px;
        text-align: left;
        padding-left: 5px;
    }

    .tree-tr{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        padding-right: 8px;
        height:41px;
    }
    .tree-title{
        border: 1px solid #ddd;
        border-bottom: 0;
        background: #fff;
        span{
            font-weight: 900;
        }
    }
</style>



